.player {
	position: fixed;
    background: #fff;
    width: 100%;
    bottom: 0;
    left: 0;
    box-sizing: border-box;
    border-top: 1px solid #ddd;
    padding: 0px 15px;
}

.ios .player {
    border-bottom: var( --safe-area-inset-bottom ) solid transparent;
}

.theme-dark .player {
	background: #262626;
	border-top: 1px solid #333;
}