.c-player__cover {
    border-radius: 10px;
    background-size: contain !important;
    background-position: center !important;
    background-repeat: no-repeat !important;
    width: 300px;
    height: 300px;
    max-width: 100%;
    margin: 0 auto;
    margin-bottom: 20px;
    background: #ddd;
}

.c-player__cover--animate {
    animation: player_cover 0.2s ease-in-out;
}

@keyframes player_cover {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}